.van-progress {
  position: relative;
  height: 0.17067rem;
  height: var(--progress-height, 0.17067rem);
  border-radius: 0.17067rem;
  border-radius: var(--progress-height, 0.17067rem);
  background: #ebedf0;
  background: var(--progress-background-color, #ebedf0);
}
.van-progress__portion {
  position: absolute;
  left: 0;
  height: 100%;
  border-radius: inherit;
  background: #1989fa;
  background: var(--progress-color, #1989fa);
}
.van-progress__pivot {
  position: absolute;
  top: 50%;
  box-sizing: border-box;
  min-width: 3.6em;
  text-align: center;
  word-break: keep-all;
  border-radius: 1em;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
  color: #fff;
  color: var(--progress-pivot-text-color, #fff);
  padding: 0 0.21333rem;
  padding: var(--progress-pivot-padding, 0 0.21333rem);
  font-size: 0.42667rem;
  font-size: var(--progress-pivot-font-size, 0.42667rem);
  line-height: 1.6;
  line-height: var(--progress-pivot-line-height, 1.6);
  background-color: #1989fa;
  background-color: var(--progress-pivot-background-color, #1989fa);
}
